.header {
    position: relative;
    margin: 30px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    nav {
        display: flex;
        align-items: center;
    }

    &::after {
        content: '';

        position: absolute;
        width: 1440px;
        height: 1px;
        bottom: -30px;
        right: -20px;
        background-color: #F4F0EC;
    }
}

.header-logo {
    display: flex;
    align-items: center;
    text-decoration: none;

    svg {
        margin-right: 10px;
    }
}

@media screen and (max-width: 1200px) {
    .header {
        margin: 20px 20px;
        &::after {
            content: '';
            width: 100%;
            bottom: -20px;
            right: 0;
        }
    }
    .header-logo {
        h2 {
            margin-top: 5px;
            font-size: 20px;
        }
        svg {
            width: 32px;
            height: 32px;
            margin-right: 5px;
        }
    }
}
@media screen and (max-width: 1200px) {
    .header {
        justify-content: space-around;
    }
}
@media screen and (max-width: 768px) {
    .header {
        margin: 10px;
    }
}
@media screen and (max-width: 520px) {
    .header {
        justify-content: space-between;
    }
}